import i18n from "i18next";
import {initReactI18next} from "react-i18next";

const resources = {
    en: {
        translation: {
            "main-text": "Welcome",
            "main-btn": "Main",
            "news-btn": "News",
            "login-btn": "Login",
            "logout-btn": "Logout",
            "profile-btn": "Profile",
            "submit-bnt": "Login",
            "email": "Email",
            "password": "Password",
            "404": "Page not found",
            "news-title": 'News',
            "download-btn": 'Download more'
        }
    },
    ua: {
        translation: {
            "main-text": " ",
            "main-btn": "",
            "news-btn": "",
            "login-btn": "",
            "logout-btn": "",
            "profile-btn": "",
            "submit-bnt": "",
            "email": " ",
            "password": "",
            "404": "  ",
            "news-title": '',
            "download-btn": ' '
        }
    }
};

i18n.use(initReactI18next)
    .init({
        resources,
        lng: "ua",
        interpolation: {
            escapeValue: false
        }
    });

export default i18n;